<script>
    import guangdong from '../assets/JSON/guangdong'
    import * as d3 from 'd3'
    let a = 1;
    export default {
        name: 'HelloWorld',
        props: {
            msg: String
        },
        data: function () {
            return {
                width: 1000,
                height: 1000,
                projection: d3.geoMercator().center([107, 31]).scale(850).translate([this.width / 2, this.height / 2]),
                path: d3.geoPath().projection(this.projection)
            }
        },
        mounted: function () {
            console.log(guangdong);
            console.log(this.width);
            const
                svg = d3.select('.hello').append('svg')
                    .attr('width', this.width)
                    .attr('height', this.height)
                    .append('g')
                    .attr('transform', 'translate(0, 0)'),
                projection = d3.geoMercator().center([107, 31]).scale(850).translate([this.width / 2, this.height / 2]),
                path = d3.geoPath().projection(projection),
                color = d3.interpolateMagma;
            svg.selectAll('path')
                .data(guangdong.features)
                .enter()
                .append('path')
                .attr('stroke', '#000')
                .attr('stroke-width', 1)
                .attr('fill', (d, i) => color(i))
                .attr('d', path)


        }
    }
